import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/product-template',
        component: () => import('../views/product-template/ProductTemplate')
    },
    {
        path: '/product-template-demo',
        component: () => import('../views/product-template/TemplateDemo')
    }
]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL + 'admin',
    routes
})

export default router
