import Vue from 'vue'
import Vuex from 'vuex'
import { getJsonResult } from '@/utils/server-wrapper'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        productList: null,
        dragElement: null
    },
    getters: {
        productList: async state => {
            if (!state.productList) {
                state.productList = await getJsonResult('/codeRecord/getProductList')
            }
            return state.productList
        }
    },
    mutations: {
        setDragElement(state, dragElement) {
            state.dragElement = dragElement
        }
    },
    actions: {},
    modules: {}
})
