import eventBus from './event-bus'
import * as server from './server'

export function errorHint(error) {
    console.log('error', error)
    let message
    if (error.message) {
        message = error.message || error.msg
    } else {
        message = error.statusText
    }
    eventBus.$emit('showMessage', {
        showClose: true,
        type: 'error',
        message
    })
}

export function wrapResult(promise) {
    return promise.catch(e => {
        errorHint(e)
        throw e
    })
}

export function wrapJsonResult(promise) {
    return wrapResult(promise).then(data => {
        if (data.code == null) {
            return Promise.resolve(data)
        } else if (data.code === 200 || data.code === 0) {
            return Promise.resolve(data.data)
        } else {
            if (data.code == 2001) {
                eventBus.$emit('showMessage', {
                    showClose: true,
                    type: 'error',
                    message: data.msg
                })
                setTimeout(() => {
                    parent.window.location.href = '/#/login'
                }, 1000)
            }
            console.log('error is', data)
            errorHint(data)
            return Promise.reject(data)
        }
    })
}

export function wrapJsonResultV1(promise) {
    return wrapResult(promise).then(data => {
        if (data.code == null) {
            return Promise.resolve(data)
        } else if (data.code === 200 || data.code === 0) {
            return Promise.resolve(data)
        } else {
            console.log('error is', data)
            errorHint(data)
            return Promise.reject(data)
        }
    })
}

export function getJsonResult(url, arg = {}, prefix) {
    return wrapJsonResult(server.getJson(url, arg, prefix))
}

export function postJsonReturn(url, arg = {}) {
    return wrapJsonResult(server.postJson(url, arg).then(r => r.json()))
}

export function postJsonReturnV1(url, arg = {}) {
    return wrapJsonResultV1(server.postJson(url, arg).then(r => r.json()))
}
